.main {
   display: flex;
   flex-direction: column;
   &__container {
      display: flex;
      flex-direction: column;
      flex: 1;
   }
   // .main__content
   &__content {
      flex: 1;
      position: relative;
   }
   // .main__video
   &__video {
      border-radius: rem(32);
      overflow: hidden;
      object-fit: cover;
      height: calc(100vh - var(--header-height) - rem(40));
   }
   // .main__play
   &__play {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: rem(240);
      height: rem(240);
   }
   // .main__icon
   &__icon {
      position: absolute;
      left: rem(32);
      bottom: rem(32);
      width: rem(128);
      aspect-ratio: 1/1;
   }
   // .main__alert
   &__alert {
      position: absolute;
   }
}
.alert-main {
   display: flex;
   position: absolute;
   bottom: rem(32);
   left: rem(175);
   background: linear-gradient(180deg, rgba(255, 255, 255, 0.7) 0%, #fff 90%);
   backdrop-filter: blur(rem(40));
   border-radius: rem(24);
   &::before {
      @include pseudo;
      width: rem(49);
      height: rem(69);
      background-image: url(../img/svg/pointer.svg);
      background-repeat: no-repeat;
      background-position: center;
      background-size: contain;
      position: absolute;
      left: rem(-14);
      bottom: 0;
      z-index: -1;
   }
   // .alert-main__text
   &__text {
      display: flex;
      justify-content: center;
      flex-direction: column;
      color: #111;
      max-width: rem(712);
      font-weight: 600;
      font-size: rem(24);
      line-height: 150%;
      padding: rem(24) rem(40);
   }
   // .alert-main__text--accent
   &__text--accent {
      display: block;
      color: #a600a8;
   }
   // .alert-main__image
   &__image {
      display: flex;
      align-items: center;
      padding: rem(30);
      border-left: rem(1) solid transparent;
      border-image: linear-gradient(180deg, rgba(34, 34, 34, 0) 0%, rgba(34, 34, 34, 20%) 50%, rgba(34, 34, 34, 0) 100%);
      border-image-slice: 1;
   }
   // .alert-main__qr
   &__qr {
      width: rem(96);
      height: rem(96);
   }
}
